$prefix: '.rise-blocks';

#{$prefix}-profile-card {
    height: 100%;
    
    #{$prefix}-profile-card-image{
        background-size: cover;
        background-repeat: no-repeat;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #ddd;
        transition: all 0.2s linear;
        i{
            font-size: 35px;
            color: #a1a1a1;
            width: inherit;
            height: initial;
        }
    }

    #{$prefix}-description-wrapper{
        #{$prefix}-profile-card-title{
            >*{
                margin:0;
            }
        }

        #{$prefix}-profile-card-description{
            p{
                margin: 0;
            }
        }

        #{$prefix}-profile-card-designation{
            > span{
                border: 1px solid transparent;
            }
        }
    }
}  

#{$prefix}-rectangle-layout{
    #{$prefix}-profile-card {
        #{$prefix}-profile-card-image{
            i{
                font-size: 50px;
            }
        }
    }
}



/* Social Media */
$social-icon-width: 40px;

#{$prefix}-social-media {
    margin-top: 25px;
    
	a {
        float: left;
        display: inline-flex;
        align-items: center;
        justify-content: center;
	    font-size: 19px;
	    transition: all .3s;
	    height: $social-icon-width;
        width: $social-icon-width;
        line-height: $social-icon-width;
        margin-right: 6px;
        margin-bottom: 6px;
        text-decoration: none;
    }	
    
}